body {
  box-sizing: border-box;
  margin: 0;
}

.wrapper {
  justify-content: center;
  align-items: center;
  height: 100vh;
  display: flex;
}

.stopwatch {
  border: 1vmin dotted #000;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  width: 80vmin;
  height: 80vmin;
  display: flex;
  position: relative;
}

.stopwatch__hand {
  transform-origin: bottom;
  position: absolute;
}

.stopwatch__hand--minute {
  background-color: #0700ff;
  width: 3vmin;
  height: 20vmin;
  animation: 600s steps(60, end) infinite time;
}

.stopwatch__hand--second {
  background-color: #2c8000;
  width: 1.5vmin;
  height: 38vmin;
  animation: 10s steps(60, end) infinite time;
}

.stopwatch__center {
  background-color: #f6a603;
  border-radius: 50%;
  width: 5vmin;
  height: 5vmin;
  display: flex;
  position: absolute;
}

@keyframes time {
  0% {
    transform: translate(0, -50%)rotate(0);
  }

  100% {
    transform: translate(0, -50%)rotate(360deg);
  }
}

